        description : 'Akty terroru indywidualnego (skrytobójstwa) były używane od początków historii ludzkości. O stosowaniu terroryzmu można mówić np. w przypadku antycznej Grecji, gdzie zabójstwo tyrana uważano za usprawiedliwione. Seneka wprost stwierdził, iż dla bogów nie ma milszej ofiary, niż krew tyrana. Przykładem skrytobójstwa było zabicie Juliusza Cezara przez spiskowców 15 marca 44 r. p.n.e. W Izraelu w I w. n.e. skrytobójstwem posługiwały się antyrzymskie stronnictwa zelotów i sykariuszy. W okresie od XI do XIII wieku na Bliskim Wschodzie systematycznie stosowali terror asasyni (hasziszijun) – izmaelicka sekta specjalizująca się w skrytobójczych zamachach. W średniowieczu teorię uzasadniającą zabójstwo tyrana stworzył Jan z Salisbury a rozwinęli ją w XVI w. monarchomachowie.Pojęcie „terroru” pojawiło się podczas rewolucji francuskiej – tak nazywano okres rządów Komitetu Bezpieczeństwa Publicznego a zasiadających w komitecie jakobinów zwano „terrorystami”. Słowo to oznaczało wówczas formę rządów polegającą na praktyce stosowania krwawych represji. Później stworzono pojęcie „terroru indywidualnego”, którym nazwano ataki opozycjonistów skierowane przeciwko przedstawicielom aparatu państwowego i klasy panującej.',
