import React from 'react';
import './Heading.css';

class Heading extends React.Component {
    state = {
        zawiniety : false,
    }

    goNext = (event) => {
        event.target.classList.toggle('zawinietybtn');
        document.body.classList.add('overflow');
        this.setState({zawiniety : true});
    } 

    render(){
        return (
            <div className={this.state.zawiniety ? "zawinieta HeroImage" : "HeroImage"}>
                <h1 className="h1">Terroryzm i Cyberterroryzm</h1>
                <h2 className="h2">Krystian Zieja kl 1i</h2>
                <div className="btn">
                    <button onClick={this.goNext} className="hbtn">Przejdź dalej</button>
                </div>
            </div>
        )
    }
}

export default Heading;